@import '../../helpers.scss';

@include with-ground-color('color-popover-background', $light: 'base-90', $dark: 'base-20');

.popover {
  position: absolute;
  left: -9999px;
  min-width: pxToRem(132px);
  max-width: pxToRem(528px);
  opacity: 1;
  transition: opacity 0.2s ease;

  &.left {
    .tip {
      left: 3rem;
    }
  }

  &.center {
    .tip {
      left: 50%;
      transform: translate(-50%);

      &.top {
        transform: rotate(180deg) translate(50%);
      }
    }
  }

  &.right {
    .tip {
      right: 3rem;
    }
  }
}

.outer {
  background: var(--color-popover-background);
  border-radius: 13px;
}

[data-popper-reference-hidden] {
  opacity: 0;
  pointer-events: none;
}

.interactive {
  max-width: pxToRem(616px);

  .inner {
    padding: 2rem;
  }
}

.open {
  display: block;
}

.inner {
  position: relative;
  padding: 1.5rem;
}

.tip {
  display: block;
  position: absolute;
  width: pxToRem(36px);
  height: pxToRem(22px);
  top: pxToRem(-22px);

  svg path {
    fill: var(--color-popover-background);
  }

  &.top {
    bottom: pxToRem(-22px);
    top: auto;
    transform: rotate(180deg);
  }
}
