@import '../../helpers.scss';

@include with-ground-color('color-radiobutton-label', $light: 'base-30', $dark: 'base-90');
@include with-ground-color('color-radiobutton-label-disabled', $light: 'base-60', $dark: 'base-40');
@include with-ground-color('color-radiobutton-icon', $light: 'base-70', $dark: 'base-60');
@include with-ground-color('color-radiobutton-icon-disabled', $light: 'base-80', $dark: 'base-40');

.radioButton {
  display: flex;
  align-items: center;

  // NOTE: overwrite for global selector rule in Curlflix
  margin-bottom: 0;

  input {
    display: none;
  }

  // check + unchecked
  .icon {
    display: inline-flex;
    color: var(--color-radiobutton-icon);
    flex-shrink: 0;
  }
  input:checked ~ .icon {
    display: none;
  }

  .iconSelected {
    display: none;
  }
  input:checked ~ .iconSelected {
    display: inline-flex;
    color: var(--color-dark-blue);
  }
}

.spacing {
  margin-right: 1rem;
}

.label {
  color: var(--color-radiobutton-label);
}

.disabled {
  .icon {
    color: var(--color-radiobutton-icon-disabled);
  }
  .iconSelected {
    opacity: 0.5;
  }

  .label {
    color: var(--color-radiobutton-label-disabled);
  }
}
