@import '../colors.module.scss';

.inputLabel {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 13px;
  height: 6rem;
  background-color: var(--color-text-field-background);
  padding: 2.78125rem 2rem;
  box-sizing: border-box;

  .label {
    font-family: 'Moderat';
    font-weight: 500;
    font-size: 25px;
    line-height: 32px;
    color: var(--color-text-field-label);

    position: absolute;
    top: 31px;
    left: 32px;
    transition: 0.2s ease;
  }

  &.isTextArea {
    flex: 1;
  }

  &.noLabel {
    padding: 2rem;
  }

  &.collapsed {
    .label {
      font-size: 22px;
      line-height: 25px;
      top: 17.5px;
    }
  }

  &.active {
    .label {
      color: var(--color-text-field-label-active);
    }
  }

  &.disabled {
    .label {
      color: var(--color-text-field-label-disabled);
    }

    input, textarea {
      color: var(--color-text-field-disabled);

      &::-webkit-input-placeholder {
        color: var(--color-text-field-disabled);
      }
    }
  }
}
