@import '../../helpers.scss';

@include with-ground-color('color-modal-background', $light: 'base-90', $dark: 'base-20');
@include with-ground-color('color-modal-close', $light: 'white', $dark: 'black');

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
}

.wrapper {
  position: relative;
  display: flex;
  height: 100%;
  flex-direction: row;
  justify-content: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#000, 0.5);
}

.content {
  overflow: auto;
  margin-top: 17.25rem;
  background: var(--color-modal-background);
  border-radius: 13px;
  position: relative;
  max-height: 85.5rem;
}

.close {
  position: sticky;
  width: 3rem;
  height: 3rem;
  margin-left: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.closeCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: var(--color-base-70);
  color: var(--color-modal-close);

  svg {
    width: 1rem;
    height: 1rem;
  }
}
