@import '../../helpers.scss';

@include with-ground-color('color-button-primary', $light: 'base-20', $dark: 'base-90');
@include with-ground-color('color-button-primary-text', $light: 'base-90', $dark: 'base-20');
@include with-ground-color('color-button-primary-pressed', $light: 'base-40', $dark: 'base-70');
@include with-ground-color('color-button-primary-pressed-text', $light: 'base-90', $dark: 'base-20');
@include with-ground-color('color-button-primary-disabled', $light: 'base-90', $dark: 'base-50');
@include with-ground-color('color-button-secondary', $light: 'base-70', $dark: 'base-40');
@include with-ground-color('color-button-secondary-text', $light: 'base-20', $dark: 'base-90');
@include with-ground-color('color-button-secondary-pressed', $light: 'base-90', $dark: 'base-30');
@include with-ground-color('color-button-secondary-pressed-text', $light: 'base-20', $dark: 'base-90');
@include with-ground-color('color-button-secondary-disabled', $light: 'base-90', $dark: 'base-50');
@include with-ground-color('color-button-tertiary-text', $light: 'base-20', $dark: 'base-90');
@include with-ground-color('color-button-tertiary-pressed-text', $light: 'base-40', $dark: 'base-70');
@include with-ground-color('color-button-tertiary-disabled-text', $light: 'base-70', $dark: 'base-50');
@include with-ground-color('color-button-highlighted-disabled', $light: 'base-90', $dark: 'base-50');

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 0;
  border-radius: pxToRem(100px);
  cursor: pointer;
  font-family: Moderat;
  // needed to prevent flicker on touch devices
  -webkit-tap-highlight-color: transparent;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;
}

.primary {
  background: var(--color-button-primary);
  color: var(--color-button-primary-text);

  &:not(.disabled):active {
    background: var(--color-button-primary-pressed);
    color: var(--color-button-primary-pressed-text);
  }

  &.disabled {
    background: var(--color-button-primary-disabled);
    color: var(--color-base-60);
  }

  &.highlighted {
    background: var(--color-red);
    color: var(--color-base-90);

    &:not(.disabled):active {
      background: var(--color-light-red);
      color: var(--color-base-90);
    }

    &.disabled {
      background: var(--color-button-highlighted-disabled);
      color: var(--color-base-60);
    }
  }
}

.secondary {
  background: var(--color-button-secondary);
  color: var(--color-button-secondary-text);

  &:not(.disabled):active {
    background: var(--color-button-secondary-pressed);
    color: var(--color-button-secondary-pressed-text);
  }

  &.disabled {
    background: var(--color-button-secondary-disabled);
    color: var(--color-base-60);
  }
}

.tertiary {
  background: transparent;
  color: var(--color-button-tertiary-text);

  &:not(.disabled):active {
    color: var(--color-button-tertiary-pressed-text);
  }

  &.disabled {
    color: var(--color-button-tertiary-disabled-text);
  }
}

.large {
  height: 4.5rem;
  padding: 2px 64px;
}

.small {
  height: 3.5rem;
  padding: 2px 32px;
}

.icon {
  display: flex;
  align-items: center;

  &:first-child {
    margin-right: 0.625rem;
  }

  &:last-child {
    margin-left: 0.625rem;
  }
}

.noPadding {
  padding: 0;
}

.disabled {
  cursor: default;
}

.iconOnly {
  border-radius: 50%;
  padding: 0;

  &.large {
    width: 4.5rem;
  }

  &.small {
    width: 3.5rem;
  }

  .icon {
    margin: 0;
  }
}
