@import './colors.module.scss';

.toggle {
  display: block;
  width: 100%;
  background-color: unset;
  border: 0;
  color: var(--color-text-primary);
  padding: 0;
  text-align: left;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.defaultTrigger .wrapper {
  margin-bottom: 0.25rem;
}

.decoration {
  height: 96px;

  &:active {
    background-color: var(--color-accordion-pressed-background);
  }

  .wrapper {
    min-height: 40px;
    padding: 0 4.75rem 0 1.875rem;
  }

  .label {
    flex-shrink: 0;
    margin-right: 1.5rem;
  }

  .arrow {
    position: absolute;
    right: 2rem;
    pointer-events: none;
    display: flex;
    align-items: center;
  }
}
