.spinner {
  animation: spin 2s linear infinite;

  // NOTE: overwrite for global selector rule in Curlflix
  // remove once we get rid of bootstrap package, or update it to 5.0
  // details: https://github.com/twbs/bootstrap/blob/v4.2.1/scss/_reboot.scss#L255
  overflow: visible;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
