:root {
  /* palette */
  --color-black: #191c23;
  --color-base-20: #20242d;
  --color-base-30: #2a2e37;
  --color-base-40: #4a505d;
  --color-base-50: #666e7f;
  --color-base-60: #999fac;
  --color-base-70: #c4c6cc;
  --color-base-80: #e9e9eb;
  --color-base-90: #f8f9f9;
  --color-white: #fff;

  /* palette as triplets, needed for rgba() */
  --color-triplet-black: 25, 28, 35;
  --color-triplet-base-20: 32, 36, 45;
  --color-triplet-base-30: 42, 46, 55;
  --color-triplet-base-40: 74, 80, 93;
  --color-triplet-base-50: 102, 110, 127;
  --color-triplet-base-60: 153, 159, 172;
  --color-triplet-base-70: 196, 198, 204;
  --color-triplet-base-80: 233, 233, 235;
  --color-triplet-base-90: 248, 249, 249;
  --color-triplet-white: 255, 255, 255;

  /* achievement metrics */
  --color-am-strength: var(--color-dark-blue);
  --color-am-intensity: var(--color-orange);
  --color-am-calories: #e15247;
}

:root,
[data-ground='light'] {
  /* highlights */
  --color-dark-blue: #3357de;
  --color-light-blue: #a1cde5;
  --color-yellow: #ebdf78;
  --color-red: #e15247;
  --color-light-red: #ff685d;
  --color-green: #7ec97f;
  --color-orange: #f58148;

  /* highlights as triplets, needed for rgba() */
  --color-triplet-dark-blue: 51, 87, 222;
  --color-triplet-light-blue: 161, 205, 229;
  --color-triplet-yellow: 235, 223, 120;
  --color-triplet-red: 225, 82, 71;
  --color-triplet-light-red: 255, 104, 93;
  --color-triplet-green: 126, 201, 127;
  --color-triplet-orange: 245, 129, 72;

  /* semantic colors */
  --color-text-primary: var(--color-base-20);
  --color-text-secondary: var(--color-base-50);
  --color-text-tertiary: var(--color-base-60);
  --color-background-primary: var(--color-base-80);
  --color-background-secondary: var(--color-base-90);
  --color-background-tertiary: var(--color-white);

  /* semantic as triplets, needed for rgba() */
  --color-triplet-background-primary: var(--color-triplet-base-80);
  --color-triplet-background-secondary: var(--color-triplet-base-90);
  --color-triplet-background-tertiary: var(--color-triplet-base-white);

  --color-button-old-primary: var(--color-red);
  --color-button-old-primary-disabled: var(--color-base-70);
  --color-button-old-primary-disabled-text: var(--color-base-50);
  --color-button-old-secondary: var(--color-base-30);
  --color-button-old-secondary-disabled: var(--color-base-70);
  --color-button-old-secondary-disabled-text: var(--color-base-60);
  --color-button-old-tertiary: var(--color-base-30);
  --color-button-old-tertiary-disabled: var(--color-base-60);
  --color-button-old-icon-tertiary: var(--color-black);
  --color-button-old-icon-tertiary-disabled: var(--color-base-60);

  --color-am-default: var(--color-base-30);
  --color-am-default-background: var(--color-base-80);
  --color-am-strength-background: #d1d9f3;
  --color-am-intensity-background: #f6e1d7;
  --color-am-calories-background: #f2d8d5;
}

[data-ground='dark'] {
  /* highlights */
  --color-dark-blue: #3971f4;
  --color-blue: #6795ff;
  --color-light-blue: #a1cde5;
  --color-yellow: #ebdf78;
  --color-red: #e15247;
  --color-light-red: #ff685d;
  --color-green: #7ec97f;
  --color-orange: #f58148;

  /* highlights as triplets, needed for rgba() */
  --color-triplet-dark-blue: 57, 113, 244;
  --color-triplet-blue: 103, 149, 255;
  --color-triplet-light-blue: 161, 205, 229;
  --color-triplet-yellow: 235, 223, 120;
  --color-triplet-red: 225, 82, 71;
  --color-triplet-light-red: 255, 104, 93;
  --color-triplet-green: 126, 201, 127;
  --color-triplet-orange: 245, 129, 72;

  /* semantic colors */
  --color-text-primary: var(--color-base-90);
  --color-text-secondary: var(--color-base-60);
  --color-text-tertiary: var(--color-base-40);
  --color-background-primary: var(--color-base-20);
  --color-background-secondary: var(--color-base-30);
  --color-background-tertiary: var(--color-base-40);

  /* semantic as triplets, needed for rgba() */
  --color-triplet-background-primary: var(--color-triplet-base-20);
  --color-triplet-background-secondary: var(--color-triplet-base-30);
  --color-triplet-background-tertiary: var(--color-triplet-base-40);

  --color-button-old-primary: var(--color-red);
  --color-button-old-primary-disabled: var(--color-base-70);
  --color-button-old-primary-disabled-text: var(--color-base-50);
  --color-button-old-secondary: var(--color-base-90);
  --color-button-old-secondary-disabled: var(--color-base-70);
  --color-button-old-secondary-disabled-text: var(--color-base-60);
  --color-button-old-tertiary: var(--color-base-90);
  --color-button-old-tertiary-disabled: #666666;
  --color-button-old-icon-tertiary: var(--color-base-90);
  --color-button-old-icon-tertiary-disabled: var(--color-base-60);

  --color-am-default: var(--color-base-90);
  --color-am-default-background: var(--color-base-20);
  --color-am-strength-background: #1f2947;
  --color-am-intensity-background: #45302b;
  --color-am-calories-background: #40272a;
}

[data-ground='legacy'] {
  --color-chip-text: #2e3e80;
  --color-chip-background: transparent;
  --color-chip-disabled: #98a0be;
  --color-tag-border: #2e3e80;
  --color-tabs-line: #2e3e80;
  --color-select: #2e3e80;
  --color-multiselect: #2e3e80;

  --color-text-primary: #2e3e80;
  --color-button-old-icon-tertiary: #2e3e80;

  --chip-width: 15rem;
  --chip-height: 4.5rem;
  --chip-border: 1px solid var(--color-chip-text);
  --chip-border-radius: 0.5rem;
  --chip-padding-horizontal: 2.1rem;
}
