@import './foundation/colors/colors.css';

html {
  // make font and spacing base explicit
  font-size: 16px;
  // make numbers align correctly
  // see https://helpx.adobe.com/fonts/using/open-type-syntax.html#lnum
  // see https://helpx.adobe.com/fonts/using/open-type-syntax.html#pnum
  font-feature-settings: 'pnum' on, 'lnum' on;
}
