@import '../../helpers.scss';

@include with-ground-color('color-listitem', $light: 'white', $dark: 'base-30');
@include with-ground-color('color-listitem-pressed', $light: 'base-80', $dark: 'base-20');

.listItem {
  background: var(--color-listitem);

  // color is defined here, to allow color="inherit" for icons provided through *Component props
  color: var(--color-text-primary);

  // needed to prevent flicker on touch devices
  -webkit-tap-highlight-color: transparent;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;

  &:active {
    background-color: var(--color-listitem-pressed);
  }
}
