@import '../../helpers.scss';

@include with-ground('color-navigation-bar', $light: 'rgba(233, 233, 235, 20%)', $dark: 'rgba(25, 28, 35, 80%)');

.navigationBar {
  // color is defined here, to allow color="inherit" for icons provided through *Component props
  color: var(--color-text-primary);
}

.side {
  > * {
    margin-left: 2rem;

    &:first-child {
      margin-left: 0;
    }
  }
}
