@import './colors.module.scss';

.toggle {
  position: relative;
  width: 100%;
  height: 72px;
  border: 1px solid var(--color-multiselect);
  background-color: var(--color-multiselect-background);
  border-radius: 8px;
  color: var(--color-text-primary);
  display: block;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;
}

.toggleOpen {
  border-width: 2px;
}

.toggleDisabled {
  color: var(--color-multiselect-disabled);
  border-color: var(--color-multiselect-disabled);
}

.wrapper {
  position: absolute;
  top: -1px;
  left: -1px;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4.75rem 0 1.875rem;
  box-sizing: border-box;
  overflow: hidden;
}

.wrapperOpen {
  top: -2px;
  left: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
}

.label {
  flex-shrink: 0;
  margin-right: 1.5rem;
}

.values {
  flex-shrink: 0;
}

.arrow {
  position: absolute;
  right: 1.875rem;
  pointer-events: none;
  display: flex;
  align-items: center;
}
