.carousel {
  position: relative;
  overflow: scroll;
  height: 100%;
  width: 100%;
  white-space: nowrap;
  touch-action: pan-y;
  user-select: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.spacer {
  display: inline-block;
}

.container {
  display: inline-block;

  > * {
    display: inline-block;
    white-space: initial;
  }
}
