/**
 * Convert a value in pixels to a value in rems.
 */
@function pxToRem($size) {
  $remSize: calc($size / 16px);
  @return #{$remSize}rem;
}

/**
 * Define css variable for any property that depends on ground.
 */
@mixin with-ground($name, $light, $dark) {
  @at-root {
    :root,
    [data-ground='light'] {
      --#{$name}: #{$light};
    }
    [data-ground='dark'] {
      --#{$name}: #{$dark};
    }
  }
}

/**
 * Convenience helper to define css variable setting a color, that depends on ground.
 */
@mixin with-ground-color($name, $light, $dark) {
  @include with-ground($name, 'var(--color-#{$light})', 'var(--color-#{$dark})');
}
