@import '../../helpers.scss';

@include with-ground-color('color-checkbox-label', $light: 'base-30', $dark: 'base-90');
@include with-ground-color('color-checkbox-label-disabled', $light: 'base-60', $dark: 'base-40');
@include with-ground-color('color-checkbox-icon', $light: 'base-70', $dark: 'base-60');
@include with-ground-color('color-checkbox-icon-disabled', $light: 'base-80', $dark: 'base-40');

@include with-ground('opacity-checkbox-icon-disabled', $light: 0.5, $dark: 1);

.checkBox {
  display: flex;
  align-items: center;
  &.topAlign {
    align-items: flex-start;
  }

  // NOTE: overwrite for global selector rule in Curlflix
  margin-bottom: 0;

  input {
    display: none;
  }

  .icon {
    display: inline-flex;
    color: var(--color-checkbox-icon);
    flex-shrink: 0;
  }
  input:checked ~ .icon {
    display: none;
  }

  .iconSelected {
    display: none;
    flex-shrink: 0;
  }
  input:checked ~ .iconSelected {
    display: inline-flex;
    color: var(--color-dark-blue);
    background-color: #fff;
    border-radius: 5px; // NOTE - 5px used instead of 4px, to avoid white px fragments in the corners
  }
}

.spacing {
  margin-right: 1rem;
}

.label {
  color: var(--color-checkbox-label);
}
.topAlign .label {
  margin-top: -0.25rem;
}

.disabled {
  .icon {
    color: var(--color-checkbox-icon-disabled);
  }
  .iconSelected {
    opacity: var(--opacity-checkbox-icon-disabled);
  }

  .label {
    color: var(--color-checkbox-label-disabled);
  }
}
