@import '../../helpers.scss';

@include with-ground('color-progress-circle', $light: '#e5e7ea', $dark: '#424242');

$progress-circle-animation-time: 2s;
$progress-circle-animation-type: cubic-bezier(0.17, 0.88, 0.44, 1);

.smallAnimation {
  animation: smallProgress $progress-circle-animation-time $progress-circle-animation-type;
}

.mediumAnimation {
  animation: mediumProgress $progress-circle-animation-time $progress-circle-animation-type;
}

.largeAnimation {
  animation: largeProgress $progress-circle-animation-time $progress-circle-animation-type;
}

.extraLargeAnimation {
  animation: extraLargeProgress $progress-circle-animation-time $progress-circle-animation-type;
}

.tickAnimation {
  visibility: hidden;
  // forwards makes the animated element stay after animation finishes
  animation: tickReveal 1s $progress-circle-animation-type forwards;
  animation-delay: 0.75s;
}

@keyframes smallProgress {
  0% {
    stroke-dasharray: 0 113;
  }
}

@keyframes mediumProgress {
  0% {
    stroke-dasharray: 0 201;
  }
}

@keyframes largeProgress {
  0% {
    stroke-dasharray: 0 490;
  }
}

@keyframes extraLargeProgress {
  0% {
    stroke-dasharray: 0 726;
  }
}

// animation starts hidden (due to the delay of animation and svg path being rendered no matter what)
// in 50% we make it visible and set the animated prop
// 100% sets visibility to keep using "forwards", over the visibility defined on class
@keyframes tickReveal {
  0% {
    stroke-dasharray: 0 100;
  }
  50% {
    visibility: visible;
    stroke-dasharray: 100 100;
  }
  100% {
    visibility: visible;
  }
}

.track {
  stroke: var(--color-progress-circle);
}
