@import './colors.module.scss';

.toggle {
  position: relative;
  width: 100%;
  background-color: unset;
  border: 0;
  color: var(--color-text-primary);
  display: block;
  padding: 0;
  text-align: left;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;
}

.decoration {
  border-radius: 8px;
  height: 72px;
  border: 1px solid var(--color-select);
  background-color: var(--color-select-background);

  &.toggleOpen {
    border-width: 2px;
  }

  &.toggleDisabled {
    color: var(--color-select-disabled);
    border-color: var(--color-select-disabled);
  }

  .wrapper {
    position: absolute;
    top: -1px;
    left: -1px;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4.75rem 0 1.875rem;
    box-sizing: border-box;
    overflow: hidden;
  }

  .wrapperOpen {
    top: -2px;
    left: -2px;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
  }

  .label {
    flex: 1 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .arrow {
    position: absolute;
    right: 1.875rem;
    pointer-events: none;
    display: flex;
    align-items: center;
  }
}
