@import '../../helpers.scss';

@include with-ground-color('color-chip-text', $light: 'black', $dark: 'white');
@include with-ground-color('color-chip-background', $light: 'white', $dark: 'black');
@include with-ground-color('color-chip-selected', $light: 'white', $dark: 'black');

@include with-ground('chip-width', $light: 'fit-content', $dark: 'fit-content');
@include with-ground('chip-height', $light: '3.5rem', $dark: '3.5rem');
@include with-ground('chip-border', $light: 'none', $dark: 'none');
@include with-ground('chip-border-radius', $light: '2.5rem', $dark: '2.5rem');
@include with-ground('chip-padding-horizontal', $light: '2rem', $dark: '2rem');

.chip {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 var(--chip-padding-horizontal);
  width: var(--chip-width);
  height: var(--chip-height);
  box-sizing: border-box;
  border: var(--chip-border);
  color: var(--color-chip-text);
  border-radius: var(--chip-border-radius);
  background-color: var(--color-chip-background);
  overflow: hidden;

  // NOTE: overwrite for global selector rule in Curlflix
  margin-bottom: 0;

  &.selected {
    background: var(--color-chip-text);
    color: var(--color-chip-selected);
  }

  &.disabled {
    color: var(--color-base-60);
    border-color: var(--color-base-60);
  }

  input {
    display: none;
  }
}
