@import '../../helpers.scss';

@include with-ground-color('color-toggle-label', $light: 'base-20', $dark: 'base-90');
@include with-ground-color('color-toggle-label-disabled', $light: 'base-60', $dark: 'base-40');
@include with-ground-color('color-toggle-switch-background', $light: 'base-70', $dark: 'base-60');
@include with-ground-color('color-toggle-switch-background-disabled', $light: 'base-80', $dark: 'base-40');
@include with-ground-color('color-toggle-knob-disabled', $light: 'base-70', $dark: 'base-30');

.toggle {
  display: flex;
  align-items: center;

  &.selected {
    .switch {
      background: var(--color-dark-blue);

      &:after {
        left: calc(100% - 1.25rem);
      }
    }
  }
}

.switch {
  width: 3rem;
  height: 1.5rem;
  background: var(--color-toggle-switch-background);
  border-radius: 1rem;
  position: relative;
  transition: 0.2s ease;

  &:after {
    content: '';
    position: absolute;
    top: 0.25rem;
    left: 0.25rem;
    width: 1rem;
    height: 1rem;
    background: var(--color-white);
    border-radius: 1rem;
    transition: 0.2s ease;
  }

  input {
    height: 0;
    width: 0;
    visibility: hidden;
  }
}

.spacing {
  margin-right: 1rem;
}

.label {
  color: var(--color-toggle-label);
}

.disabled {
  opacity: 0.5;

  .label {
    color: var(--color-toggle-label-disabled);
  }

  .switch {
    background: var(--color-toggle-switch-background-disabled);
    &:after {
      background: var(--color-toggle-knob-disabled);
    }
  }

  &.selected {
    .switch {
      &:after {
        background: var(--color-white);
      }
    }
  }
}
