@import '../../helpers.scss';

@include with-ground-color('color-list-divider', $light: 'base-70', $dark: 'base-50');

.list {
  > * {
    display: block;
  }
}

.withDivider {
  > * {
    $divider-size: 2px;

    position: relative;

    &:not(:last-child) {
      // reserve a space for divider
      border-bottom: $divider-size solid transparent;

      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: $divider-size;
        bottom: -$divider-size;
        background: var(--color-list-divider);
      }
    }
  }
}

.withDividerOffset {
  > * {
    &:not(:last-child) {
      &:after {
        left: 2rem;
        right: 2rem;
      }
    }
  }
}
