@import './colors.module.scss';

.options {
  position: absolute;
  left: 0;
  width: 100%;
  border: 2px solid var(--color-multiselect);
  border-radius: 8px;
  padding: 2.25rem 1.875rem;
  box-sizing: border-box;
  max-height: 35rem;
  overflow: auto;
  background-color: var(--color-multiselect-background);
  z-index: 99;

  // NOTE: overwrite for global selector rule in Curlflix
  &::-webkit-scrollbar {
    display: initial;
    width: 18px;
  }
  // NOTE: overwrite for global selector rule in Curlflix
  &::-webkit-scrollbar-thumb {
    border-radius: 8px;
    border: 6px solid rgba(0, 0, 0, 0);
    background-clip: padding-box;
    background-color: rgba(0, 0, 0, 0.2);
  }
}

.down {
  top: calc(100% + 0.625rem);
}

.up {
  bottom: calc(100% + 0.625rem);
}
