@import '../../helpers.scss';

@mixin dimensionsInRem($width, $height) {
  width: pxToRem($width);
  height: pxToRem($height);
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 0;
  cursor: pointer;
  font-family: Moderat;
  // needed to prevent flicker on touch devices
  -webkit-tap-highlight-color: transparent;

  // NOTE: overwrite for global selector rule in Curlflix
  outline: none !important;
}

.primary {
  background: var(--color-button-old-primary);
  color: var(--color-white);

  &.disabled {
    background: var(--color-button-old-primary-disabled);
    color: var(--color-button-old-primary-disabled-text);
  }
}

.secondary {
  color: var(--color-button-old-secondary);
  border: pxToRem(2px) solid var(--color-button-old-secondary);

  &.disabled {
    color: var(--color-button-old-secondary-disabled-text);
    border-color: var(--color-button-old-secondary-disabled);
  }
}

.tertiary {
  color: var(--color-button-old-tertiary);

  &.disabled {
    color: var(--color-button-old-tertiary-disabled);
  }
}

.large {
  @include dimensionsInRem(240px, 64px);
  border-radius: pxToRem(10px);

  .icon {
    margin-right: pxToRem(12px);
  }
}

.medium {
  @include dimensionsInRem(160px, 44px);
  border-radius: pxToRem(4px);

  .icon {
    margin-right: pxToRem(8px);
  }
}

.small {
  @include dimensionsInRem(120px, 32px);
  border-radius: pxToRem(4px);

  .icon {
    margin-right: pxToRem(4px);
  }
}

.icon {
  display: flex;
  align-items: center;
  margin-right: pxToRem(12px);
}

.disabled {
  cursor: default;
}

.iconOnly {
  color: var(--color-button-old-icon-tertiary);

  .icon {
    margin-right: 0;
  }

  &.large {
    width: pxToRem(64px);
  }

  &.medium {
    width: pxToRem(44px);
  }

  &.small {
    width: pxToRem(32px);
  }

  &.disabled {
    color: var(--color-button-old-icon-tertiary-disabled);
  }

  &.circle {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    background: var(--color-base-90);
    color: var(--color-base-20);

    &:active {
      background: var(--color-base-70);
    }

    &.disabled {
      background: var(--color-base-70);
    }
  }
}
