@import './colors.module.scss';

.input {
  flex: 1;
  padding: 0;
  border: 0;
  background: transparent;
  resize: none;

  font-family: Moderat;
  font-size: 25px;
  line-height: 32px;
  font-weight: 500;
  color: var(--color-text-field);

  &:focus {
    outline: none;
  }

  &::-webkit-input-placeholder {
    color: var(--color-text-field-placeholder);
  }

  &.withIcon {
    width: calc(100% - 64px);
  }

  &.isTextArea {
    width: calc(100% - 84px);
  }
}
