@font-face {
  font-family: 'Moderat';
  font-style: normal;
  font-weight: normal;
  src: url('../../fonts/Moderat-Regular.woff2');
}

@font-face {
  font-family: 'Moderat';
  font-style: normal;
  font-weight: bold;
  src: url('../../fonts/Moderat-Bold.woff2');
}

@font-face {
  font-family: 'Moderat monospace';
  font-style: normal;
  font-weight: normal;
  src: url('../../fonts/ModeratMono-Regular.woff2');
}

.fontNormal {
  font-family: 'Moderat';
}

.fontMonospace {
  font-family: 'Moderat monospace';
}

// common modifiers

.block {
  display: block;
}

.bold {
  font-weight: bold;
}

.ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.lineClamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.leftAlign {
  text-align: left;
}

.centerAlign {
  text-align: center;
}

.rightAlign {
  text-align: right;
}

.justifyAlign {
  text-align: justify;
}
